<template>
  <div class="auth-investor-one full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="auth-investor-wrapper">
          <div class="auth-invertor-top">
            <span class="auth-investor-title">请上传名片</span>
            <span class="auth-investor-step">（1 / 2）</span>
          </div>
          <div class="auth-investor-upload">
            <input name="files" value="" type="file" accept="image/*" @change="uploadImage">
            <span class="auth-upload-text">点击上传名片</span>
          </div>
        </div>
      </pem-scroll-view>
    </div>
    <pem-toast ref="toast" :text="toastText"></pem-toast>
    <pem-loading v-show="isUploadImg" title="上传中"></pem-loading>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import PemLoading from 'components/pem-loading/pem-loading'
import { mapState, mapActions } from 'vuex'
import { AUTH_UPDATE } from 'store/minisite/auth'
import { MiniSiteApi, ImgPath } from 'api'
import { compressImage, setTitle } from 'common/js/util'

export default {
  data () {
    return {
      isLoading: true,
      toastText: '',
      isUploadImg: false
    }
  },
  components: {
    PemScrollView,
    PemToast,
    PemLoading
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-认证')
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    uploadImage (e) {
      let file = e.target.files[0]
      if (this._validateUploadImage(file)) {
        compressImage(file, blobFile => {
          if (blobFile.size > 2 * 1024 * 1024) {
            this.toast('图片文件过大，请上传小于2MB')
            return false
          }
          this.isUploadImg = true
          this._uploadNameCard(file, (data) => {
            this.isUploadImg = false
            if (+data.resultCode === 1) {
              let obj = {name: '', corpname: '', position: '', email: '', wechat: '', tel: '', address: '', cardlogo: '', imgUrl: ''}
              obj.cardlogo = `${data.uploadpath}/${data.thumbnailfileName}`
              obj.imgUrl = `${ImgPath}${data.uploadpath}/${data.thumbnailfileName}`
              if (data.distObj) {
                obj.name = data.distObj.name || ''
                obj.corpname = data.distObj.corpname || ''
                obj.position = data.distObj.position || ''
                obj.email = data.distObj.email || ''
                obj.wechat = data.distObj.wechat || ''
                obj.tel = data.distObj.fronttel && data.distObj.aftertel ? `${data.distObj.fronttel}${data.distObj.fronttel && data.distObj.aftertel && '-'}${data.distObj.aftertel}` : ''
                obj.address = data.distObj.address || ''
              }
              this.$router.push(`/auth-investor-two?d=${JSON.stringify(obj)}`)
            } else {
              let errMsg = {
                '-11': '上传图片太大',
                '-12': '上传图片太小',
                '-13': '网络错误,请重试'
              }[+data.resultCode] || '上传失败,请重试'
              this.toast(errMsg)
            }
          })
        })
      }
    },
    _validateUploadImage (file) {
      if (!file) {
        return false
      }
      if (['image/png', 'image/jpeg'].indexOf(file.type) === -1) {
        this.toast('上传图片类型不正确，请上传JPG、PNG')
        return false
      }
      return true
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    _uploadNameCard (file, callback) {
      MiniSiteApi.uploadNameCard(file).then(data => {
        callback(data)
      })
    },
    _updateInvest (options, callback) {
      MiniSiteApi.newUpdateInvest(options).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      AUTH_UPDATE
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/mixin"
  .auth-investor-wrapper
    padding: 0 0.3rem
  .auth-invertor-top
    font-size: 0
    padding: 1.06rem 0 0.32rem
    .auth-investor-title
      height: 0.6rem
      line-height: 0.6rem
      font-size: 0.44rem
      font-weight: bold
      color: #41403E
    .auth-investor-step
      font-size: 0.24rem
      color: #A7A5A3
      padding-left: 0.2rem
  .auth-investor-upload
    position: relative
    display: block
    height: 3.8rem
    border-radius: 0.04rem
    background-image: url('../../../common/image/minisite/auth/mingpian_bg@2x.png'), url('../../../common/image/minisite/auth/icon_mingp@2x.png')
    background-size: 100% 100%, 0.82rem 0.62rem
    background-position: 0 0, center 1.32rem
    background-repeat: no-repeat
    .auth-upload-text
      display: block
      text-align: center
      font-size: 0.24rem
      color: #A7A5A3
      padding-top: 2.14rem
    input
      position: absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      opacity: 0 
</style>



